<script>
import $ from 'jquery';
export default {
    data : function(){
        return {
            page_idx:0,
            blind:false,
            result_arr:[],
            mbti_list:[
                'ISTJ',
                'ISTP',
                'ISFJ',
                'ISFP',
                'INTJ',
                'INTP',
                'INFJ',
                'INFP',
                'ESTJ',
                'ESTP',
                'ESFJ',
                'ESFP',
                'ENTJ',
                'ENTP',
                'ENFJ',
                'ENFP',
            ]
        }
    },
    watch: {
        page_idx: function (newVal, oldVal) {
            $('.left_area').removeClass('hide');
            console.log(this.result_arr);
            // if(newVal==3){
            //     $('.section4>.back').removeClass('wave');
            //     setTimeout(() => {
            //         $('.section4>.back').addClass('wave');
            //     }, 250);
            // }
            if(newVal==9){
                $('.section10>.black_area').removeClass('on');
                setTimeout(() => {
                    $('.section10>.black_area').addClass('on');
                }, 250);
            }
            if(newVal==16){
                $('.section17>.back>img').removeClass('on');
                // $('.section17>.txt_box').removeClass('on');
                setTimeout(() => {
                    $('.section17>.back>img').addClass('on');
                   //  $('.section17>.txt_box').addClass('on');
                }, 500);
            }

            if(newVal==23){
                $('.left_area').addClass('hide');
                setTimeout(() => {
                    $('.section24>.black_area').addClass('on');
                }, 3000);
                setTimeout(() => {
                    this.page_idx=newVal+1;
                    $('.section24>.black_area').removeClass('on');
                }, 5000);
            }
            if(newVal==24){
                $('.section25>.black_area').removeClass('on');
                setTimeout(() => {
                    $('.section25>.black_area').addClass('on');
                }, 500);
            }
            if(newVal==40){
                $('.left_area').addClass('hide');
                // $('.section41>.back').removeClass('wave');
                // setTimeout(() => {
                //     $('.section41>.back').addClass('wave');
                // }, 250);
                setTimeout(() => {
                    this.page_idx=41;
                }, 1000);
            }
            if(newVal==41){
                $('.section42>.bubble').removeClass('wave');
                setTimeout(() => {
                    $('.section42>.bubble').addClass('wave');
                }, 250);
            }
            console.error(oldVal);
        }
    },
    methods:{
        history_back:function(){
            var page = this.page_idx;
            if(page==0){
                this.$router.push('/');
            }else{
                if(page==9 || page==24 || page==41){
                    page = page-2
                }else{
                    page = page-1;
                }
                this.page_idx=page;
            }
            console.log(page);
            if(page==12 || page==14 || page==21 || page==22 || page==24 || page==27 || page==34 || page==35 || page==39 || page==42 || page==47 || page==48){
                this.result_arr.pop();
            }
            console.log(this.result_arr);
        },
        home:function(){
            this.$router.push('/');
        },
        next_page:function(){
            var page = this.page_idx;
            this.page_idx=page+1;
        },
        black_blank:function(idx){
            var page = this.page_idx;
            if(idx==8){
                $('.section9>.black_area').addClass('on');
                setTimeout(() => {
                    this.page_idx=page+1;
                    $('.section9>.black_area').removeClass('on');
                }, 1000);
            }
        },
        choice_answer:function(mbti,e){
            var target = $(e.target);
            var target_tag;
            if(target.hasClass('.item')){
                target_tag=target;
            }else{
                target_tag=target.parents('.item');
            }
            target_tag.addClass('on');
            var page = this.page_idx;
            this.result_arr.push(mbti);
            this.blind=true;
            setTimeout(() => {
                this.page_idx=page+1;
                target_tag.removeClass('on');
                this.blind=false;
            }, 500);
        },
        choice_answer_fade:function(e){
            var target = $(e.target);
            var target_tag;
            if(target.hasClass('.item')){
                target_tag=target;
            }else{
                target_tag=target.parents('.item');
            }
            target_tag.addClass('on');
            var page = this.page_idx;
            $('.section36').removeClass('on');
            $('.section36').removeClass('back');
            this.blind=true;
            if(page==34){
                setTimeout(() => {
                    $('.section36').addClass('on');
                }, 500);
                setTimeout(() => {
                    $('.section36').addClass('back');
                }, 1500);
                setTimeout(() => {
                    target_tag.removeClass('on');
                    this.blind=false;
                }, 500);
            }
        },
        result_page:function(){
            var res_list = this.result_arr;
            var res_obj={
                e:0,i:0,s:0,t:0,j:0,p:0,n:0,f:0,
            }
            for(var res of res_list){
                switch (res) {
                    case 'I':
                        res_obj.i++;
                        break;
                    case 'E':
                        res_obj.e++;
                        break;
                    case 'S':
                        res_obj.s++;
                        break;
                    case 'T':
                        res_obj.t++;
                        break;
                    case 'F':
                        res_obj.f++;
                        break;
                    case 'P':
                        res_obj.p++;
                        break;
                    case 'N':
                        res_obj.n++;
                        break;
                    case 'J':
                        res_obj.j++;
                        break;
                    default:
                        break;
                }
            }
            var res_txt="";
            if(res_obj.i>res_obj.e){
                res_txt+="I";
            }else if(res_obj.i<res_obj.e){
                res_txt+="E";
            }
            if(res_obj.s>res_obj.n){
                res_txt+="S";
            }else if(res_obj.s<res_obj.n){
                res_txt+="N";
            }
            if(res_obj.t>res_obj.f){
                res_txt+="T";
            }else if(res_obj.t<res_obj.f){
                res_txt+="F";
            }
            if(res_obj.j>res_obj.p){
                res_txt+="J";
            }else if(res_obj.j<res_obj.p){
                res_txt+="P";
            }
            var flow_num=0;
            for(var item of this.mbti_list){
                if(item==res_txt){
                    break
                }
                flow_num++;
            }
            this.$router.push('/result?idx='+flow_num);
        }
    },
    mounted:function(){
        //나중에 section17이 오면 작동해야하는 애니메이션
        setTimeout(() => {
            $('.section17>.back>img').addClass('on');
            $('.section17>.txt_box').addClass('on');
        }, 500);
        var default_on=$('.audio_area>.wrap>.btn_area>.right_area>.switch').find('.music_on');
        var default_off=$('.audio_area>.wrap>.btn_area>.right_area>.switch').find('.music_off');
        
        var video = document.getElementById('audio_id'); 
        var is_play=video.paused;
        if(is_play){
            default_on.hide();
            default_off.hide();
            default_on.show();
        }else{
            default_on.hide();
            default_off.hide();
            default_off.show();
        }
        $('.audio_area>.wrap>.btn_area>.right_area>.switch').click(function(){
            var that = $(this);
            var on = that.find('.music_on');
            var off = that.find('.music_off');
            var on_status = on.css('display');
            var off_status = off.css('display');
            if(on_status=='none' && off_status=='block'){
                on.hide();
                off.hide();
                on.show();
                document.getElementById('audio_id').pause();
            }else if(on_status=='block' && off_status=='none'){
                on.hide();
                off.hide();
                off.show();
                document.getElementById('audio_id').play();
            }
        })
    }
}
</script>

